<template><section>
<h2>logger打印</h2>
<p>开启log的debug close关闭 openView打开在视图上 openLog打开在浏览器上 你可以更具当前的环境去动态切换 开发时候打开 打包时候关闭</p>
<pre v-pre=""><code class="hljs language-javascript">  <span class="hljs-keyword">import</span> { Logger } <span class="hljs-keyword">from</span> <span class="hljs-string">'dby-base'</span>
  Vue.use(Logger, {<span class="hljs-attr">debug</span>: <span class="hljs-string">'openLog'</span>})
  <span class="hljs-comment">// 访问 this.$logger</span>
</code></pre>
<h4>当前方法有</h4>
<pre v-pre=""><code class="hljs language-javascript">  $logger.log () 
  $logger.info ()
  $logger.warn ()
  $logger.error()
</code></pre>
</section></template>

<script lang="babel">
export default {
  mounted() {
    this.$logger.info('打印一下')
  }
}
</script>


